<script setup>
    import {
        useConfigurableOptionConfigStore,
        useConfigurableOptionStore,
    } from '@/stores/configurable_option'
    import { storeToRefs } from 'pinia'

    /**
     *
     * Charge les éléments
     * Charge le schéma de données
     *
     *
     * 1- Affichage d'une Liste
     * 2- Formulaire d'ajout / Modification d'un élément
     */

    const props = defineProps({
        url: { type: String, required: true },
        formConfigUrl: { type: String, required: true },
    })
    const dataStore = useConfigurableOptionStore()
    const configStore = useConfigurableOptionConfigStore()
    configStore.setUrl(props.formConfigUrl)

    const items = storeToRefs(dataStore.optionCollection)
    async function fetchData() {
        const promise = dataStore.setInitialValues(props.url)
        const promise2 = configStore.loadConfig()
        await Promise.all([promise, promise2])
    }
    fetchData()
</script>

<template>
    <div>
        <h2>Liste des éléments</h2>
        <ul>
            <li v-for="item in items.value" :key="item.id">
                {{ item.name }}
            </li>
        </ul>
        <!-- Formulaire d'ajout / Modification d'un élément -->
    </div>
</template>
